.footer {
  background-color: snow;
  display: flex;
}

.footer .left {
  flex: 1;
  overflow: visible;
}

.footer .left img {
  height: 100%;
}

.footer .right {
  height: 86vh;
  width: 55%;
  position: absolute;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: visible;
  right: 0;
}

.footer .right h1 {
  font-size: 50px;
  overflow: visible;
}

.footer .right form {
  height: 70%;
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.footer .right input {
  height: 30px;
  width: 300px;
  font-size: 14px;
  align-self: flex-start;
  border-radius: 6px;
  border-width: 1px;
}

.footer .right textarea {
  height: 70%;
  width: 99%;
  font-size: 14px;
  border-radius: 6px;
  border-width: 1px;
  resize: none;
}

.footer .right textarea:focus, input:focus {
  outline: none !important;
  border:1px solid #2fc18c;
}

.footer .right .fbMsg {
  height: 10%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin: 0 0 40px;
  transition: all 2s ease;
  background-color: snow;
}

.footer .right .fbMsg h3 {
  color: #2fc18c;
}

.footer .right .fbMsg.active {
  opacity: 100%;
}

.footer .right .fbMsg {
  opacity: 0%;
}

.footer .right button {
  background-color: darkslateblue;
  border: none;
  border-radius: 10px;
  color: azure;
  height: 30px;
  width: 150px;
  font-weight: 600;
  cursor: pointer;
  align-self: flex-end;
  transition: 0.3s;
}

.footer .right button.able:hover {
  background-color: #2fc18c;
}

.footer .right button.disable:hover {
  background-color: #c12f2f;
  text-decoration: line-through;
}